___CSS_LOADER_EXPORT___.push([module.id, `body {
    margin: 0;
    min-height: 100vh;
    width: 100% ;
}

.content {
    min-height: 100vh;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template: .4fr .1fr 5fr / 3fr 1fr;
    grid-template-areas: 
    'projectPanel projectPanel'
    'toolBar toolBar'
    'todoPanel todoPanel';
}

.toolBar {
    display: flex;
    justify-content: flex-end;    
    grid-area: toolBar;
    border: 1px solid purple;
    background-color: rgb(71, 57, 71);
}

.projectPanel {
    display: flex;
    justify-content: flex-start;
    grid-area: projectPanel;
    background-color: rgb(230, 230, 230);
    width: 100%;
    height: 100%;
}

.project {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-top-right-radius: 35%;
    border-top-left-radius: 35%;
    background-color: rgb(54, 4, 54);
    border: 1px solid saddlebrown;
    max-width: 150px;
    height: 100%;
    margin-left: 5px;
    padding: 0 5px;
    color: white;
}

.todoPanel {
    grid-area: todoPanel;
    background-color: rgb(57, 57, 57);
    border: 1px solid blue;
    height: 100%;
    width: 100%;
